import { FC } from 'react';
import styled from 'styled-components';
import { StyledButton, StyledText } from '../../../shared/StyledComponents';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import RegisterFormTypes from './registerForm.types';
import HookTextGlobalInput from '../../../shared/hookForm/HookTextGlobalInput';
import HookInputWithinInput from '../../../shared/hookForm/HookInputWithinInput';
import SubscriptionSelect from './SubscriptionSelect';
import { get } from 'lodash';
import Checkbox from '../../../shared/form/Checkbox';
import HookCheckbox from '../../../shared/hookForm/HookCheckbox';

const schema = yup.object().shape({
  facility_name: yup.string().required('Facility Name is required!'),
  address: yup.string().required('Address is required!'),
  state: yup.string().required('State is required!'),
  city: yup.string().required('City is required!'),
  zip: yup.string().required('Zip is required!'),
  first_name: yup.string().required('First Name is required!'),
  last_name: yup.string().required('Last Name is required!'),
  email: yup
    .string()
    .email('Email is not valid!')
    .required('email is required!'),
  phone: yup
    .string()
    .required('Phone Number is required!')
    .typeError('Phone Number is required!'),
  website: yup.string().required('Website is required!'),
  facility_type: yup
    .number()
    .required('Facility Type is required!')
    .typeError('Facility Type is required!'),
  no_of_beds: yup
    .number()
    .required('No of bed is required!')
    .typeError('No of bed is required!'),
  subscription: yup
    .number()
    .required('Subscription is required!')
    .typeError('Subscription is required!'),
  news_letter: yup.boolean(),
});

const defaultValues = {
  facility_name: '',
  address: '',
  state: '',
  city: '',
  zip: '',
  first_name: '',
  last_name: '',
  email: '',
  phone: '',
  website: '',
  facility_type: '',
  no_of_beds: '',
  subscription: '',
  news_letter: false,
};

const RegisterForm: FC = () => {
  const {
    handleSubmit,
    control,
    formState: { errors },
    setValue,
    watch,
    reset,
  } = useForm<RegisterFormTypes>({
    resolver: yupResolver(schema),
    mode: 'onChange',
    defaultValues,
  });

  const subscription = watch('subscription');
  const facilityType = watch('facility_type');

  const submitForm = (formVales: RegisterFormTypes) => {
    console.log(formVales);
    reset(defaultValues);
  };

  return (
    <form onSubmit={handleSubmit(submitForm)}>
      <div className="container mx-auto flex flex-col items-center justify-center mt-32 lg:mt-16 py-16">
        <Container className="w-11/12 lg:w-6/12 mb-8">
          <div className="w-full mb-9">
            <StyledText fs={20} lh={39} color="#DA3B19">
              Facility Registration
            </StyledText>
          </div>
          <div className="w-full flex items-center mb-7">
            <AvatarContainer />
            <StyledText className="mr-5" fs={15} lh={30} fw={400} color="#000">
              Profile Photo
            </StyledText>
            <div className="cursor-pointer">
              <StyledText fs={15} lh={30} fw={400} color="#DA3B19">
                Upload
              </StyledText>
            </div>
          </div>
          <div className="w-full flex mb-5">
            <HookTextGlobalInput
              name="facility_name"
              control={control}
              errors={errors}
              label="Facility Name"
            />
          </div>
          <div className="w-full flex mb-5">
            <HookTextGlobalInput
              name="address"
              control={control}
              errors={errors}
              label="Street Address"
            />
          </div>
          <div className="w-full flex flex-col sm:flex-row mb-5">
            <div className="mb-5 sm:mb-0 sm:w-4/12 sm:pr-1">
              <HookTextGlobalInput
                name="state"
                control={control}
                errors={errors}
                label="State"
              />
            </div>
            <div className="mb-5 sm:mb-0 sm:w-4/12 sm:px-1">
              <HookTextGlobalInput
                name="city"
                control={control}
                errors={errors}
                label="City"
              />
            </div>
            <div className="sm:w-4/12 sm:pl-1">
              <HookTextGlobalInput
                name="zip"
                control={control}
                errors={errors}
                label="Zip"
              />
            </div>
          </div>
          <div className="w-full flex flex-wrap mb-5">
            <div className="w-full">
              <StyledText className="mb-2">Facility Contact</StyledText>
            </div>
            <div className="mb-5 sm:mb-0 sm:w-6/12 sm:pr-1">
              <HookTextGlobalInput
                name="first_name"
                control={control}
                errors={errors}
                label="Fist Name"
              />
            </div>
            <div className="sm:w-6/12 sm:pl-1">
              <HookTextGlobalInput
                name="last_name"
                control={control}
                errors={errors}
                label="Last Name"
              />
            </div>
          </div>
          <div className="w-full flex flex-wrap mb-5">
            <div className="mb-5 sm:mb-0 sm:w-6/12 sm:pr-1">
              <HookTextGlobalInput
                name="email"
                control={control}
                errors={errors}
                label="Email"
              />
            </div>
            <div className="sm:w-6/12 sm:pl-1">
              <HookTextGlobalInput
                name="phone"
                control={control}
                errors={errors}
                type="number"
                label="Phone Number"
              />
            </div>
          </div>
          <div className="w-full flex flex-wrap mb-5">
            <div>
            <HookTextGlobalInput
              name="website"
              control={control}
              errors={errors}
              label="Facility Website"
            />
            </div>
          </div>
          <div className="w-full flex flex-wrap mb-5">
            <div className="w-full mb-2">
              <div className="flex items-center">
                <StyledText className="mb-2">Type of Facility</StyledText>
                {Boolean(get(errors, 'facility_type')) && (
                  <StyledText
                    className="ml-1"
                    color="red"
                    fs={13}
                    lh={15}
                    fw={500}
                    ls={0}
                  >
                    ({get(errors, 'facility_type')?.message})
                  </StyledText>
                )}
              </div>
            </div>
            {facilityList?.map((val) => (
              <div className="w-full flex items-center mb-2" key={val.value}>
                <Checkbox
                  checked={facilityType === val.value}
                  onChange={() =>
                    setValue('facility_type', val.value, {
                      shouldDirty: true,
                      shouldValidate: true,
                    })
                  }
                />
                <StyledText color="#000" fs={20} lh={20} fw={400} ls={0}>
                  {val.label}
                </StyledText>
              </div>
            ))}
          </div>
          <div className="w-full flex mb-5">
            <HookInputWithinInput
              name="no_of_beds"
              control={control}
              errors={errors}
              label="Number of BEDS"
            />
          </div>
        </Container>
        <Container className="w-11/12 lg:w-6/12 p-9">
          <div className="w-full mb-6">
            <div className="flex items-center">
              <StyledText color="#DA3B19" fs={20} lh={30} fw={600} ls={0}>
                Select Subscription
              </StyledText>
              {Boolean(get(errors, 'subscription')) && (
                <StyledText
                  className="ml-1"
                  color="red"
                  fs={13}
                  lh={15}
                  fw={500}
                  ls={0}
                >
                  ({get(errors, 'subscription')?.message})
                </StyledText>
              )}
            </div>
            <StyledText color="#000" fw={400}>
              Every Model includes unlimited subscriptions
            </StyledText>
          </div>
          <div className="w-full flex flex-col justify-center items-center sm:flex-row">
            {subscriptionsList?.map((val) => (
              <SubscriptionSelect
                onChange={() =>
                  setValue('subscription', val.value, {
                    shouldDirty: true,
                    shouldValidate: true,
                  })
                }
                key={val.value}
                price={val.price}
                description={val.description}
                isActive={subscription === val.value}
              />
            ))}
          </div>
        </Container>
        <div className="flex w-6/12 my-5">
          <div className="flex items-center">
            <HookCheckbox name="news_letter" control={control} />
            <StyledText fs={15} lh={20} ls={0} color="#000" fw={400}>
              Do you wish to receive our weekly newsletter?
            </StyledText>
          </div>
        </div>
        <div className="flex w-6/12 items-center justify-center">
          <StyledButton type="submit" className="px-20 py-4">
            <StyledText fs={17} fw={700} ls={0.3} color="white">
              Register
            </StyledText>
          </StyledButton>
        </div>
      </div>
    </form>
  );
};

export default RegisterForm;

const Container = styled.div`
  padding: 32px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 40px #00000012;
`;

const AvatarContainer = styled.div`
  width: 90px;
  height: 90px;
  border: 1px solid #bababa;
  border-radius: 100%;
  margin-right: 20px;
`;

const subscriptionsList = [
  {
    value: 1,
    price: '$39.95',
    description: 'per month',
  },
  {
    value: 2,
    price: '$99.95',
    description: 'every three month',
  },
  {
    value: 3,
    price: '$249.95',
    description: 'per year',
  },
];

const facilityList = [
  {
    value: 1,
    label: 'Independent Living Facility (ILF)',
  },
  {
    value: 2,
    label: 'Assisted Living Facility (ALF)',
  },
  {
    value: 3,
    label: 'Personal Care Home (PCH)',
  },
  {
    value: 4,
    label: 'Skilled Nursing Unit (SNU)',
  },
  {
    value: 5,
    label: 'Long Term Care Facility (LTC)',
  },
  {
    value: 6,
    label: 'Secured Dementia Unit (SDU)',
  },
  {
    value: 7,
    label: 'Long Term Acute Care Facility (LTAC)',
  },
  {
    value: 8,
    label: 'In Home Care (IHC)',
  },
];
